.footerBanner {

  display: flex;
  flex-direction: row-reverse;
  margin: 3em auto 5em auto;



  img {

      width: calc(60% - 1rem);

  }



  .footerBannerInner {
      position: relative;
      background-color: black;
      padding: 2em 0;
      width: 40%;
      display: flex;
      justify-content: center;
      align-items: center;
      flex-direction: column;





      h2 {
          @extend .display-l-36-;
          color: $white;
          text-align: center;
          padding: 0 !important;



      }

      p {
          @extend .regular-body-s-14-;
          color: $white;
          padding: 1em;
          // margin: 1em 0;

      }

      .shopDevicesBtn {

        
          color: white;
          background: transparent;
          border: 1px solid white;
          display: flex;
          justify-content: start;

      }

      .mapiconWrapper {
          position: absolute;
          left: 0%;
          bottom: 10%;
          display: flex;
          flex-direction: column;
          align-items: flex-start;
         margin-left: -1em;

          .mapPinImg {
              width: 5%;
              margin-bottom: 0.75em;

          }

          .mapMaxHeaderImg {
              width: 20%;
              margin-left: -1em;

          }

      }


  }





  .standingMan {
      padding: 1em 0 1em 0;

  }

}